import { Grid } from '@material-ui/core';
import { Pagination, PaginationItem } from '@material-ui/lab';
import { Link } from 'gatsby';
import React from 'react';
import { BlogPost } from '../../models/blog-post';
import NoPostsFound from '../no-posts-found/no-posts-found';
import PostSummary from '../post-summary/post-summary';

export interface PostSummaryListProps {
  posts: BlogPost[];
  currentPageNumber: number;
  totalPageCount: number;
}

export function PostSummaryList({
  posts,
  currentPageNumber,
  totalPageCount,
}: PostSummaryListProps) {
  if (!posts || !posts.length) {
    return <NoPostsFound />;
  }

  return (
    <>
      <Grid container spacing={5}>
        {posts.map((post) => {
          return (
            <Grid item xs={12} sm={6} md={4} key={post.id}>
              <PostSummary post={post}></PostSummary>
            </Grid>
          );
        })}
      </Grid>
      {totalPageCount > 0 && (
        <Pagination
          color="primary"
          page={currentPageNumber}
          count={totalPageCount}
          renderItem={(item) => (
            <PaginationItem
              component={Link}
              to={`/blogs/${item.page}`}
              {...item}
            />
          )}
        />
      )}
    </>
  );
}

export default PostSummaryList;
